/* Move down content because we have a fixed navbar that is 3.5rem tall */
body {
  padding-top: 1.5rem;
  font-size: 85%;
}

.form-control {
  font-size: 100%;
}

.custom-select {
  height: calc(2.25rem - 2px);
  margin-right: 0px !important;
}

.tight-label {
  margin-right: 3px !important;
}

.custom-input {
  margin-right: 0px !important;
}

#tubeMapSVG {
  overflow: auto;
  width: calc(100% - 40px);
  position: relative;
  margin: 20px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  max-height: 75vh;
}

.custom-file-control:after {
  content: attr(data-content) !important;
}

.color-box {
  width: 15px;
  height: 15px;
  display: inline-block;
  background-color: #ccc;
  /* position: absolute;*/
  left: 5px;
  top: 5px;
}

.smallLoader {
  border: 5px solid #f3f3f3;
  animation: spin 1s linear infinite;
  border-top: 5px solid #555;
  border-radius: 50%;
  width: 30px;
  height: 30px;

  /* position: absolute; */
  z-index: 1;
  /* top: 50%; */
  /* margin: -25px 0 0 0px; */
  position: relative;
  /* top: -30px; */
  left: 21px;
  margin-right: -21px;
  background-color: #6fa9fb;
}

#loader {
  border: 5px solid #f3f3f3;
  animation: spin 1s linear infinite;
  border-top: 5px solid #555;
  border-radius: 50%;
  width: 50px;
  height: 50px;

  position: absolute;
  z-index: 1;
  top: 50%;
  margin: -25px 0 0 0px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

input[type='file'] {
  width: 200px;
}

